//   "medium": "40em", 
//   "large": "59.375em", 
//   "xlarge": "87.5em",
// );

// 639px, 1149px, 1399px
$breakpoints-down: (
  "small": " 23.4375em", 
  "medium": "53.75em", 
  "large": "90em",
);

// @mixin desktop($size) {
//   @media (min-width: map-get($breakpoints-up, $size )){
//     @content;
//   }
// }

@mixin mobile($size) {
  @media (max-width: map-get($breakpoints-down, $size )){
    @content;
  }
}

@mixin tablet($size) {
  @media (max-width: map-get($breakpoints-down, $size )){
    @content;
  }
}

@mixin desktop($size) {
  @media (min-width: map-get($breakpoints-down, $size )){
    @content;
  }
}
