html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text-2);
}

:root {
  --very-dark-blue: hsl(233, 47%, 7%);
  --dark-desat-blue: hsl(244, 38%, 16%);
  --soft-violet: hsl(277, 64%, 61%);
  --neutral-white: hsl(0, 0%, 100%);
  --trans-main-white: hsla(0, 0%, 100%, 0.75);
  --trans-stats-white: hsla(0, 0%, 100%, 0.6);
}

:root {
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

body {
  background-color: var(--very-dark-blue);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.container {
  max-width: 21.6666666667rem;
  margin: 0 auto;
}
@media (min-width: 90em) {
  .container {
    max-width: 96rem;
  }
}

.card {
  width: 21.6666666667rem;
  height: 52rem;
  border-radius: 0.6666666667rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.card_img {
  height: 16rem;
  width: 100%;
  background: url(../../../images/image-header-mobile.jpg) no-repeat center center/cover;
  filter: brightness(150%);
}
.card_img .overlay {
  width: 100%;
  height: 100%;
  background-color: var(--soft-violet);
  opacity: 0.9;
  mix-blend-mode: multiply;
}
.card_text {
  padding: 0.6666666667rem;
  background-color: var(--dark-desat-blue);
  height: 36rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.card_text h1 {
  margin: 0;
  padding: 1.3333333333rem;
  font-size: 1.6666666667rem;
  font-weight: 700;
  color: var(--neutral-white);
}
.card_text span {
  color: var(--soft-violet);
}
.card_text p {
  padding: 0 1.3333333333rem;
  margin-bottom: 0;
  color: var(--trans-main-white);
  font-size: 0.8666666667rem;
  font-weight: 200;
  line-height: 1.5;
  letter-spacing: 0.0333333333rem;
}
.card_text--stats .stat:not(:last-child) {
  padding-bottom: 1.3333333333rem;
}
.card_text--stats h2 {
  margin: 0;
  color: var(--neutral-white);
}
.card_text--stats p {
  margin: 0;
  padding-top: 0.3333333333rem;
  font-family: var(--font-lexend);
  font-size: 0.7333333333rem;
  font-weight: 200;
  color: var(--trans-stats-white);
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}
@media (min-width: 90em) {
  .card {
    width: 74rem;
    height: 29.6666666667rem;
    flex-direction: row-reverse;
  }
  .card .card_img {
    height: 29.6666666667rem;
    width: 36rem;
    background: url(../../../images/image-header-mobile.jpg) no-repeat center center/cover;
    filter: brightness(150%);
  }
  .card .card_img .overlay {
    width: 100%;
    height: 100%;
    background-color: var(--soft-violet);
    opacity: 0.9;
    mix-blend-mode: multiply;
  }
  .card .card_text {
    width: 38rem;
    height: 100%;
    padding: 0 3.3333333333rem;
  }
  .card .card_text--content {
    text-align: left;
  }
  .card .card_text--content p {
    padding-right: 6.6666666667rem;
  }
  .card .card_text--stats {
    padding-left: 1.3333333333rem;
    display: flex;
    justify-content: left;
    text-align: left;
  }
  .card .card_text--stats p {
    padding: 0;
  }
  .card .card_text--stats .stat {
    padding-right: 3.3333333333rem;
  }
}