@use '../util/' as *;

body {
  background-color: var(--very-dark-blue);

  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;
}

.container {
  // padding: 0 rem(25);
  max-width: rem(325);
  margin: 0 auto;

  
  
  @include desktop(large) {
    max-width: rem(1440);

  }
}