@use '../util' as *;

.card {
  width: rem(325);
  height: rem(780);
  
  border-radius: rem(10);
  overflow: hidden;

  display: flex;
  flex-direction: column;
  // justify-content: space-between;

  text-align: center;

  &_img {
    height: rem(240);
    width: 100%;
    background: url(../../../images/image-header-mobile.jpg) no-repeat center center / cover;

    .overlay {
      width: 100%;
      height: 100%;
      background-color: var(--soft-violet);
      opacity: .9;
      mix-blend-mode: multiply;
    }

    filter: brightness(150%);

  }

  &_text {
    padding: rem(10);

    background-color: var(--dark-desat-blue);
    height: rem(780 - 240);

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    h1 {
      margin: 0;
      padding: rem(20);

      font-size: rem(25);
      font-weight: 700;
      color: var(--neutral-white);
    }

    span {
      color: var(--soft-violet);
    }

    p {
      padding: 0 rem(20);
      margin-bottom: 0;

      color: var(--trans-main-white);
      font-size: rem(13);
      font-weight: 200;
      line-height: 1.5;
      letter-spacing: rem(0.5);
    }

    &--stats {
      .stat:not(:last-child) {
        padding-bottom: rem(20);
      }
      h2 {
        margin: 0;
        color: var(--neutral-white);
      }
      p {
        margin: 0;
        padding-top: rem(5);

        font-family: var(--font-lexend);
        font-size: rem(11);
        font-weight: 200;
        color: var(--trans-stats-white);
        text-transform: uppercase;
        letter-spacing: rem(1.5);
      }
    }
  }




  
  @include desktop(large) {
    width: rem(1110);
    height: rem(445);

    flex-direction: row-reverse ;

  
    .card_img {
      height: rem(445);
      width: rem(1110 - 570);
      background: url(../../../images/image-header-mobile.jpg) no-repeat center center / cover;

      .overlay {
        width: 100%;
        height: 100%;
        background-color: var(--soft-violet);
        opacity: .9;
        mix-blend-mode: multiply;
      }

      filter: brightness(150%);

    }

    .card_text {
      width: rem(570);
      height: 100%;

      padding: 0 rem(50);
      
      &--content {
        text-align: left;

        p {
          padding-right: rem(100);
        }
      }
      
      &--stats {
        padding-left: rem(20);
        display: flex;
        justify-content: left;

        text-align: left;

        p {
          padding: 0;
        }

        .stat {
          padding-right: rem(50);
        }
      }
    }

  }
}
